import { Form } from 'react-bootstrap'
import { Button } from 'react-bootstrap';
import { Container, Row, Col, Card, InputGroup, FormControl } from 'react-bootstrap'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faUserCircle, faKey } from '@fortawesome/free-solid-svg-icons'
import Image from 'react-bootstrap/Image'
import logo from '../images/logo.png'
import facebook from '../images/icons8-facebook.svg'
import instagram from '../images/icons8-instagram.svg'
import twitter from '../images/icons8-twitter.svg'
import './ResetPasswordApply.css'
import Swal from 'sweetalert2'
//import withReactContent from 'sweetalert2-react-content'

//const MySwal = withReactContent(Swal)

const userRequested = document.getElementById("userRequested").getAttribute("data-user");;

var applyChange = async function () {

  let password = document.getElementById("password").value;
  let password2 = document.getElementById("password2").value

  const swalWithBootstrapButtons = Swal.mixin({
    customClass: {
      confirmButton: 'btn btn-success',
      cancelButton: 'btn btn-danger'
    },
    buttonsStyling: false
  });

  let title = "<strong>Verificación de Datos</strong>";
  let text = "Las contraseñas no coinciden";
  let icon = "error";

  if (password === password2 && password !== "") {
    text = "Cambio de contraseña correcto. \n Redireccionando a Login en <b></b> milliseconds";
    icon = "success";
  }

  let timerInterval;
  await swalWithBootstrapButtons.fire({
    title: title,
    html: text,
    timer: 6000,
    icon: icon,
    timerProgressBar: true,
    didOpen: () => {
      Swal.showLoading()
      const b = Swal.getHtmlContainer().querySelector('b')
      timerInterval = setInterval(() => {
        b.textContent = Swal.getTimerLeft()
      }, 100)
    },
    willClose: () => {
      clearInterval(timerInterval)
    }
  }).then((result) => {
    /* Read more about handling dismissals below */
    if (result.dismiss === Swal.DismissReason.timer) {
      //console.log('I was closed by the timer')
      window.location.href = "http://localhost:3001/";
    }
  })
};

